.broadcastView {
  padding: 2rem 0 2rem 0; }

.pageTitle {
  margin: 2rem 0 2rem 0; }

.textarea {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 2px;
  outline: none;
  transition: all 0.3s;
  box-sizing: border-box;
  resize: vertical;
  max-height: 480px; }
  .textarea:focus, .textarea:active {
    border-color: #008380;
    transition: all 0.28s; }

.small {
  composes: textarea;
  padding: 8px 11px;
  font-size: 14px;
  line-height: 18px; }

.normal {
  composes: textarea;
  padding: 11px;
  font-size: 16px;
  line-height: 24px; }
